* {
  box-sizing: border-box;
}

.body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars_star {
  width: 16px;
  height: 16px;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars_star:not(:first-child) {
  margin-left: 4px;
}

.stars--1 :not(:nth-of-type(n+2)),
.stars--2 :not(:nth-of-type(n+3)),
.stars--3 :not(:nth-of-type(n+4)),
.stars--4 :not(:nth-of-type(n+5)),
.stars--5 :not(:nth-of-type(n+6)) {
  background-image: url(images/star-active.svg);
}
